// ChatRoom.tsx

import React, { useState, useEffect, useRef } from 'react';
import { useWebSocket } from '@/hooks/useWebSocket';
import { getChatHistory, markMessagesAsRead } from '@/api/chatService/chat';
import type { ChatMessage, ChatMessageDto } from '@/types/chatService/chat';
import { Send, ArrowLeft } from 'lucide-react';

interface ChatRoomProps {
  roomId: string;
  currentUserId: string;
  currentUserName: string;
  roomName: string; // âœ… Props ì •ì˜ëŠ” ë˜ì–´ ìˆìŒ
  onBack: () => void; // âœ… Props ì •ì˜ëŠ” ë˜ì–´ ìˆìŒ
}

// ğŸ’¡ ìˆ˜ì •: Propsë¥¼ ëª¨ë‘ êµ¬ì¡° ë¶„í•´ í• ë‹¹í•˜ê³ , Header UI/ê¸°ëŠ¥ì„ ì¶”ê°€í–ˆìŠµë‹ˆë‹¤.
export default function ChatRoom({ roomId, currentUserId, currentUserName, roomName, onBack }: ChatRoomProps) {
  const [messages, setMessages] = useState<ChatMessage[]>([]);
  const [inputMessage, setInputMessage] = useState('');
  const [loading, setLoading] = useState(false);
  const messagesEndRef = useRef<HTMLDivElement>(null);

  // ìë™ ìŠ¤í¬ë¡¤ ê¸°ëŠ¥
  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  // WebSocket ì—°ê²°
  const { isConnected, sendMessage } = useWebSocket({
    url: 'http://localhost:8084/ws-chat',
    roomId,
    onMessage: (newMessage: ChatMessage) => {
      console.log('ğŸ“© ìƒˆ ë©”ì‹œì§€ ìˆ˜ì‹ :', newMessage);
      setMessages((prev) => [...prev, newMessage]);

      // ìë™ ìŠ¤í¬ë¡¤
      scrollToBottom();

      // ìƒëŒ€ë°© ë©”ì‹œì§€ë©´ ì½ìŒ ì²˜ë¦¬
      // ğŸ’¡ ìˆ˜ì •: newMessage.userSingId -> newMessage.userSignId (ì˜¤íƒ€ ìˆ˜ì •)
      if (newMessage.userSignId !== currentUserId) {
        markMessagesAsRead(roomId, currentUserId).catch(console.error); // ğŸ’¡ userIdë¥¼ APIë¡œ ì „ë‹¬í•´ì•¼ í•©ë‹ˆë‹¤.
      }
    },
  });

  // ì±„íŒ… íˆìŠ¤í† ë¦¬ ë¡œë“œ
  useEffect(() => {
    loadChatHistory();
  }, [roomId]);

  const loadChatHistory = async () => {
    setLoading(true);
    try {
      // ğŸ’¡ ìˆ˜ì •: getChatHistoryëŠ” í˜ì´ì§€ë„¤ì´ì…˜ì„ ì§€ì›í•˜ì§€ë§Œ, ì—¬ê¸°ì„œëŠ” ì²« í˜ì´ì§€ë¥¼ ë¡œë“œí•©ë‹ˆë‹¤.
      const history = await getChatHistory(roomId);
      setMessages(history.reverse()); // ìµœì‹  ë©”ì‹œì§€ê°€ ì•„ë˜ë¡œ ì˜¤ë„ë¡ ìˆœì„œ ë°˜ì „
    } catch (err) {
      console.error('ì±„íŒ… íˆìŠ¤í† ë¦¬ ë¡œë“œ ì‹¤íŒ¨:', err);
      setMessages([]);
    } finally {
      setLoading(false);
      scrollToBottom();
    }
  };

  const handleSendMessage = () => {
    if (!inputMessage.trim() || !isConnected) return;

    const messageDto: ChatMessageDto = {
      roomId,
      userSignId: currentUserId,
      name: currentUserName,
      content: inputMessage,
      type: 'TALK',
    };

    sendMessage(messageDto);
    setInputMessage('');
  };

  const handleKeyPress = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === 'Enter') {
      handleSendMessage();
    }
  };

  const formatTime = (dateString?: string) => {
    if (!dateString) return '';
    const date = new Date(dateString);
    return date.toLocaleTimeString('ko-KR', { hour: '2-digit', minute: '2-digit' });
  };

  return (
      <div className="flex flex-col h-full bg-white">
        {/* ğŸ’¡ ì¶”ê°€: Header ì˜ì—­ */}
        <div className="flex items-center p-4 border-b border-gray-200 bg-white sticky top-0 z-10">
          <button onClick={onBack} className="md:hidden p-2 mr-2 text-gray-600 hover:text-blue-600 transition">
            <ArrowLeft className="w-5 h-5" />
          </button>
          <h2 className="text-lg font-semibold text-gray-800 truncate">{roomName}</h2>
        </div>

        {/* ë©”ì‹œì§€ ì˜ì—­ */}
        <div className="flex-1 overflow-y-auto p-4 space-y-4">
          {loading ? (
              <div className="text-center text-gray-500">ëŒ€í™” ë‚´ìš©ì„ ë¶ˆëŸ¬ì˜¤ëŠ” ì¤‘...</div>
          ) : (
              messages.map((message) => {
                const isMyMessage = message.userSignId === currentUserId;

                return (
                    <div
                        key={message.chatId}
                        className={`flex ${isMyMessage ? 'justify-end' : 'justify-start'}`}
                    >
                      <div className={`flex flex-col max-w-xs lg:max-w-md ${isMyMessage ? 'items-end' : 'items-start'}`}>
                        {/* ë°œì‹ ì ì´ë¦„ (ë‚´ ë©”ì‹œì§€ê°€ ì•„ë‹ ê²½ìš°) */}
                        {!isMyMessage && (
                            <span className="text-xs font-semibold text-gray-600 mb-1">
                      {message.name || message.userSignId}
                    </span>
                        )}

                        {/* ë©”ì‹œì§€ ë‚´ìš© ë²„ë¸” */}
                        <div
                            className={`p-3 rounded-xl shadow-md ${
                                isMyMessage
                                    ? 'bg-blue-500 text-white rounded-br-none' // ë‚´ ë©”ì‹œì§€ ìŠ¤íƒ€ì¼
                                    : 'bg-gray-200 text-gray-800 rounded-tl-none' // ìƒëŒ€ë°© ë©”ì‹œì§€ ìŠ¤íƒ€ì¼
                            }`}
                        >
                          {message.content}
                        </div>
                        <div className="text-xs text-gray-400 mt-1">
                          {formatTime(message.createdAt)}
                        </div>
                      </div>
                    </div>
                );
              })
          )}
          <div ref={messagesEndRef} />
        </div>

        {/* ì…ë ¥ì°½ */}
        <div className="border-t p-4 bg-white">
          <div className="flex space-x-2">
            <input
                type="text"
                value={inputMessage}
                onChange={(e) => setInputMessage(e.target.value)}
                onKeyPress={handleKeyPress}
                placeholder="ë©”ì‹œì§€ë¥¼ ì…ë ¥í•˜ì„¸ìš”..."
                disabled={!isConnected}
                className="flex-1 px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 disabled:bg-gray-100"
            />
            <button
                onClick={handleSendMessage}
                disabled={!isConnected || !inputMessage.trim()}
                className="px-6 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600 disabled:bg-gray-300 disabled:cursor-not-allowed transition"
            >
              <Send className="w-5 h-5" />
            </button>
          </div>
        </div>
      </div>
  );
}